<template>
    <!-- SideBar -->
    <div id="drawer-navigation" class="fixed top-0 right-0 z-40 w-[310px] h-screen overflow-y-auto transition-all  bg-white"
        tabindex="-1" aria-labelledby="drawer-navigation-label">
        <div class="bg-[#EDAC24] p-4">
            <h5 id="drawer-navigation-label" class="text-base font-semibold text-white uppercase">Menu</h5>
            <button type="button" @click="closeSidebar"
                class="text-white bg-transparent hover:bg-white hover:text-[#EDAC24] rounded-lg text-sm p-1.5 absolute top-2.5 right-2.5 inline-flex items-center">
                <svg aria-hidden="true" class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd"
                        d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                        clip-rule="evenodd"></path>
                </svg>
                <span class="sr-only">Close menu</span>
            </button>
        </div>
        <ul class="lg:hidden p-4 pl-14 flex flex-col gap-4">
            <li><a href="#" class="hover:border-b-2 border-[#EDAC24]">Shipping</a></li>
            <li><a href="#" class="hover:border-b-2 border-[#EDAC24]">Pricing</a></li>
            <li><a href="#" class="hover:border-b-2 border-[#EDAC24]">Contact</a></li>
        </ul>
        <div class="p-4 overflow-y-auto">
            <ul class="space-y-2 font-medium">
                <template v-if="token">
                    <li>
                        <router-link to="/account"
                            class="flex items-center p-2 text-gray-900 hover:text-white rounded-lg hover:bg-[#EDAC24] group">
                            <i class="fas fa-chart-line"></i>
                            <span class="ml-3">Dashboard</span>
                        </router-link>
                    </li>
                    <!-- <li>
                        <router-link to="/account"
                            class="flex items-center p-2 text-gray-900 rounded-lg hover:text-white hover:bg-[#EDAC24] group">
                            <i class="fas fa-box"></i>
                            <span class="flex-1 ml-3 whitespace-nowrap">Packages</span>
                        </router-link>
                    </li> -->
                    <li>
                        <a href="#" @click="logout"
                            class="flex items-center p-2 text-gray-900 rounded-lg hover:text-white hover:bg-[#EDAC24] group">
                            <i class="fas fa-sign-in-alt"></i>
                            <span class="flex-1 ml-3 whitespace-nowrap">Logout</span>
                        </a>
                    </li>
                </template>

                <template v-else>
                    <li>
                        <router-link to="/login"
                            class="flex items-center p-2 text-gray-900 rounded-lg hover:text-white hover:bg-[#EDAC24] group">
                            <i class="fas fa-sign-in-alt"></i>
                            <span class="flex-1 ml-3 whitespace-nowrap">Sign In</span>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/signup"
                            class="flex items-center p-2 text-gray-900 rounded-lg hover:text-white hover:bg-[#EDAC24] group">
                            <i class="fas fa-user-plus"></i>
                            <span class="flex-1 ml-3 whitespace-nowrap">Sign Up</span>
                        </router-link>
                    </li>
                </template>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: "SidebarComponent",
    data() {
        return {
            token: null
        };
    },
    methods: {
        closeSidebar() {
            this.$emit('close-sidebar');
        },
        logout() {
            localStorage.removeItem("token");
            localStorage.removeItem("username");
            this.$router.push("/login");
        },
    },
    mounted() {
        this.token = localStorage.getItem('token');
    }
};
</script>