<template>
    <nav class="fixed w-full z-[999] text-base font-medium bg-[#f8eaca] h-[65px]">
        <div class="container mx-auto flex justify-between items-center gap-0 lg:px-10 px-3 h-full">

            <router-link to="/" class="lg:self-auto self-start">
                <!-- <h1 class="font-bold text-3xl">Self Ship <span class="text-[#EDAC24]">Label</span> </h1> -->
                <img class="h-[60px] w-[70px] object-cover" src="../assets/images/logo.png" alt="logo">
            </router-link>

            <ul class="hidden lg:flex gap-4">
                <li><router-link to="/" class="hover:border-b-2 border-[#EDAC24]">Ship Now</router-link></li>
                <li><router-link to="/about" class="hover:border-b-2 border-[#EDAC24]">About</router-link></li>
                <li><router-link to="/contact-us" class="hover:border-b-2 border-[#EDAC24]">Contact us</router-link></li>
            </ul>

            <div class="flex items-center lg:gap-4 lg:text-base text-sm">
                <div class="lg:block hidden space-x-2">
                    <template v-if="token">
                        <router-link to="/account" class="py-2 px-3 bg-[#ffd16e]">{{name}}</router-link>
                    </template>
                    <template v-else>
                        <router-link to="/login">Login</router-link>
                        <router-link to="/signup" class="py-2 px-3 bg-[#ffd16e]">Sign up</router-link>
                    </template>
                </div>
                <div class="text-xl text-[#3e2f0f]">
                    <button type="button" class="py-1 px-3 bg-[#ffd16e]" @click="sidebar = true">
                        <i class="fa-solid fa-bars"></i>
                    </button>
                </div>
            </div>

            <SidebarComponent v-if="sidebar" @close-sidebar="closeSidebar">
            </SidebarComponent>
        </div>
    </nav>
</template>

<script>
import SidebarComponent from './SidebarComponent.vue';

export default {
    name: "NavbarComponent",
    data() {
        return {
            sidebar: false,
            token: null,
            name: null,
        };
    },
    components: { SidebarComponent },
    methods: {
        closeSidebar() {
            this.sidebar = false;
        },
    },
    mounted() {
        this.token = localStorage.getItem('token');
        this.name = localStorage.getItem('name');
    }
};
</script>