	</template>
	<template v-else>
		<NavbarComponent></NavbarComponent>
		<div class="pt-[63px]">
			<router-view />
		</div>
		<FooterComponent></FooterComponent>
	</template>
</template>

<script>
import NavbarComponent from "./components/NavbarComponent.vue";
import FooterComponent from "./components/FooterComponent.vue";

export default {
	name: "App",
