<template>
    <LoaderComponent v-if="loading"></LoaderComponent>
    <section class="container mx-auto py-5 sm:px-10 px-3 space-y-5">
        <StepComponent></StepComponent>
        <DangerAlert :message="errors.message"></DangerAlert>

        <div class="grid 2xl:grid-cols-8 gap-5">
            <div class="2xl:col-span-6 py-10 px-5 bg-[#F5F3F6] rounded">
                <h3 class="mb-4 text-2xl font-semibold leading-none text-gray-900"> Confirm your booking</h3>
                <p class="block mb-2 text-sm text-gray-900">Confirm your booking effortlessly and take control of your
                    shipments. Quick and easy, our confirmation process ensures your packages are on the way to their
                    destination with just a few clicks.</p>
                <div class="grid 2xl:grid-cols-3 justify-center items-start gap-5 py-5">
                    <div class="bg-white shadow-md rounded-xl p-3 h-full">
                        <b>Package:</b>
                        <p class="pt-2">{{ pkg.service_label }}</p>
                        <p>{{ pkg.package_type_code }}</p>
                        <template v-for="box in pkg.boxes" :key="box.id">
                            <p>
                                Dimension: {{ box.length }} x {{ box.width }} x {{ box.height }} {{ box.dim_unit }} <br>
                                Weight: {{ box.weight }} {{ box.weight_unit }}
                            </p>
                        </template>
                    </div>
                    <div class="bg-white shadow-md rounded-xl p-5 h-full">
                        <b>From:</b>
                        <p class="pt-2"><b>Name:</b> {{ pkg?.ship_from?.fullname }}</p>
                        <p><b>Address:</b> {{ pkg?.ship_from?.address }}</p>
                        <p v-if="pkg?.ship_from?.address_2"><b>Address 2:</b> {{ pkg?.ship_from?.address_2 }}</p>
                        <p v-if="pkg?.ship_from?.address_3"><b>Address 3:</b> {{ pkg?.ship_from?.address_3 }}</p>
                        <p><b>Zip code:</b> {{ pkg?.ship_from?.zip_code }}</p>
                        <p><b>City:</b> {{ pkg?.ship_from?.city }}</p>
                        <p><b>Country:</b> {{ pkg?.ship_from?.country?.name }}</p>
                    </div>
                    <div class="bg-white shadow-md rounded-xl p-5 h-full">
                        <b>To:</b>
                        <p class="pt-2"><b>Name:</b> {{ pkg?.ship_to?.fullname }}</p>
                        <p><b>Address:</b> {{ pkg?.ship_to?.address }}</p>
                        <p v-if="pkg?.ship_to?.address_2"><b>Address 2:</b> {{ pkg?.ship_to?.address_2 }}</p>
                        <p v-if="pkg?.ship_to?.address_3"><b>Address 3:</b> {{ pkg?.ship_to?.address_3 }}</p>
                        <p><b>Zip code:</b> {{ pkg?.ship_to?.zip_code }}</p>
                        <p><b>City:</b> {{ pkg?.ship_to?.city }}</p>
                        <p><b>Country:</b> {{ pkg?.ship_to?.country?.name }}</p>
                    </div>
                </div>

                <div class="space-y-5 bg-[#ffffd9] p-4">
                    <h3 class="text-xl font-semibold">Terms & Conditions</h3>
                    <!-- <ul class="space-y-2">
                        <li>I hereby acknowledge and accept the terms and conditions of ShippingXPS Self Service for my purchase. </li>
                    </ul> -->

                    <p class="flex items-center gap-5">
                        <input type="checkbox" class="h-[20px] w-[20px]" @click="this.terms_agree_1 = true">
                        I hereby acknowledge and accept the terms and conditions of Self Ship Label for my purchase.
                    </p>
                    <p class="flex items-center gap-5">
                        <input type="checkbox" class="h-[20px] w-[20px]" @click="this.terms_agree_2 = true">
                        I confirm there are NO restricted, prohibited, hazardous materials, or dangerous goods in this shipment. 
                    </p>
                    <p class="flex items-center gap-5">
                        <input type="checkbox" class="h-[20px] w-[20px]" @click="this.terms_agree_3 = true">
                        I understand that if an item in my shipment is undeclared, restricted, prohibited, hazardous or a dangerous good, it will be discarded without reimbursement.
                    </p>
                    <p class="flex items-center gap-5">
                        <input type="checkbox" class="h-[20px] w-[20px]" @click="this.terms_agree_4 = true">
                        I understand I may be billed more if the package exceeds the weight and dimensions I provided.
                    </p>
                    <p class="flex items-center gap-5">
                        <input type="checkbox" class="h-[20px] w-[20px]" @click="this.terms_agree_5 = true">
                        I understand that the recipient may be charged for duties and taxes upon delivery. Final costs are determined by customs authorities at time of import into the destination country.
                    </p>
                </div>
            </div>

            <div class="2xl:col-span-2 bg-[#F5F3F6]">
                <div class="w-full py-3 text-center bg-[#f7a900] text-white font-bold"><i
                        class="fa-solid fa-circle-exclamation"></i> <span>Shipment Information</span></div>
                <div class="p-4 space-y-5">
                    <h4 class="font-medium text-xl">{{ pkg.service_label }}</h4>
                    <!-- <p>2 to 5 business days delivery</p> -->
                    <ul class="space-y-3">
                        <template v-if="pkg.pkg_ship_type == 'international'">
                            <p> <i class="fa-solid fa-shield-halved text-xl"></i> Customs value: <span
                                    class="text-gray-700">${{
                                        pkg.shipping_total }}</span>
                                <router-link class="text-[#f7a900]" to="/customs">
                                    Edit</router-link>
                            </p>
                        </template>
                        <!-- <li><i class="fa-regular fa-circle-check text-xl"></i> Parcel Information <a class="text-[#f7a900]"
                                href="#"> View</a></li>
                        <li><i class="fa-regular fa-circle-check text-xl"></i> Tax Information <a class="text-[#f7a900]"
                                href="#"> View</a></li>
                        <li><i class="fa-regular fa-circle-check text-xl"></i> Customs declaration <a class="text-[#f7a900]"
                                href="#"> View</a></li> -->
                        <li><i class="fa-regular fa-circle-check text-xl"></i> Restricted and prohibited items <a
                                class="text-[#f7a900]" href="/shipping-restrictions"> View</a></li>
                    </ul>
                </div>
                <div class="w-full py-3 text-center bg-[#f7a900] text-white font-bold"><span>Price breakdown:</span></div>

                <ul class="space-y-3 p-4">
                    <li class="flex justify-between items-center"><span>Shipping charges:</span> <span>${{
                        pkg.shipping_charges ?? 0 }}</span></li>
                    <li class="flex justify-between items-center text-xl font-medium"><span>Total:</span>
                        <span>${{ pkg.grand_total ?? 0 }}</span>
                    </li>
                </ul>
            </div>
        </div>

        <DangerAlert :message="errors.message"></DangerAlert>

        <div class="flex justify-between items-center sm:px-10 py-5">
            <BackButton></BackButton>
            <button type="button" @click="payment"
                class="text-white bg-[#ffcb5a] hover:bg-[#f7a900] focus:ring-4 focus:outline-nonefont-medium rounded-lg text-sm px-5 py-2.5 text-center">
                Confirm & Pay
            </button>
        </div>
    </section>
</template>

<script>
import DangerAlert from './Global/Alerts/DangerAlert.vue';
import BackButton from './Global/BackButton.vue';
import LoaderComponent from './Partials/LoaderComponent.vue';
import StepComponent from './StepComponent.vue';

export default {
    name: "BookingConfirmation",
    data() {
        return {
            loading: false,
            terms_agree_1: false,
            terms_agree_2: false,
            terms_agree_3: false,
            terms_agree_4: false,
            terms_agree_5: false,
            pkg: {},
            errors: {},
            form: {
                package_id: ''
            },
            project_url: process.env.VUE_APP_PROJECT_URL,
        };
    },
    components: { BackButton, StepComponent, DangerAlert, LoaderComponent },
    methods: {
        fetchPackage() {
            this.loading = true;
            const url = "package/get-package"
            this.$axios
                .post(url, null, {
                    headers: this.$headers,
                })
                .then((response) => {
                    this.pkg = response.data.data.package;
                    this.loading = false;
                })
                .catch((error) => {
                    console.error(error);
                    this.loading = false;
                });
        },
        payment() {
            if (this.terms_agree_1 && this.terms_agree_2 && this.terms_agree_3 && this.terms_agree_4 && this.terms_agree_5) {
                this.errors = {};
                this.loading = true;
                this.form.package_id = this.pkg.id;
                this.$axios
                    .post("package/payment", this.form, {
                        headers: this.$headers
                    })
                    .then((response) => {
                        window.location.href = this.project_url + '/square-payment/' + response.data.data.package_id;
                    })
                    .catch((error) => {
                        this.errors = error.response.data;
                    })
                    .finally(() => {
                        this.loading = false;
                    });
            } else {
                this.errors.message = 'Kindly agree to the terms and conditions by clicking.';
            }
        },
    },
    mounted() {
        this.fetchPackage();
    }
};
</script>