<template>
    <section class="container mx-auto py-10 lg:px-20 px-3">
        <div class="text-center bg-yellow-50 xl:py-3 xl:mt-14 lg:text-base text-sm mx-auto w-fit" role="alert">
            <span class="p-4 xl:px-10 px-3 text-yellow-900 rounded-lg  xl:leading-none sm:leading-[3] leading-[2.5]">

                <template v-if="loading">
                    <b>Please wait we are fetching best rates for you.</b>
                </template>

                <template v-else>
                    <template v-if="rates.length > 0">
                        <span class="font-semibold">Please note:</span> All shipments must be dropped off with the carrier
                        and a printer is required for all services.
                    </template>

                    <template v-else>
                        <span class="text-red-600">No rates were found. Please try a different location or dimensions.</span>
                    </template>
                </template>
            </span>
        </div>

        <div class="xl:py-4 pt-12 space-y-5" v-if="rates.length > 0 && !loading">
            <h4 class="font-bold text-xl">
                {{ rates.length }} Results Found
            </h4>

            <div class="xl:space-y-3 space-y-2">
                <template v-for="rate in rates" :key="rate.id">
                    <div
                        class="w-full xl:h-[140px] shadow-md lg:pr-4 bg-white border rounded-lg flex justify-between items-center lg:flex-row flex-col overflow-hidden">
                        <div class="flex items-center lg:gap-5 gap-3 h-full lg:flex-row flex-col">

                            <img class="lg:w-[200px] w-screen object-cover xl:h-full h-[100px] lg:rounded-l-lg bg-purple-300"
                                src='../assets/images/fedex-logo.jpg' alt="" v-if="rate.code == 'fedex'">
                            <img class="lg:w-[200px] w-screen object-cover xl:h-full h-[100px] lg:rounded-l-lg bg-purple-300"
                                src='../assets/images/ups-logo.jpg' alt="" v-if="rate.code == 'ups'">
                            <img class="lg:w-[200px] w-screen object-cover xl:h-full h-[100px] lg:rounded-l-lg bg-purple-300"
                                src='../assets/images/dhl-logo.jpg' alt="" v-if="rate.code == 'dhl'">

                            <div class="lg:w-[300px] lg:text-left text-center">
                                <h4 class="font-semibold text-xl">{{ rate.name }}</h4>
                                <a href="#" class="hover:border-b-2 hover:border-gray-700">More about {{ rate.code }}
                                    Shipping</a>
                            </div>
                        </div>
                        <div class="lg:w-[320px]">
                            <!-- <p> <i class="fa-regular fa-circle-check"></i> 10 - 22 Business Days</p> -->
                            <!-- <p> <i class="fa-regular fa-circle-check"></i> Global Door-to-Door Tracking</p> -->
                        </div>
                        <div
                            class="lg:w-[450px] flex xl:gap-20 lg:gap-0 gap-3 xl:flex-row flex-col items-center justify-between">
                            <div class="lg:w-[200px]">
                                <h4 class="font-bold xl:text-4xl text-2xl break-words">${{ rate.total }}</h4>
                            </div>
                            <button @click="setRate(rate)"
                                class="bg-[#EDAC24] lg:w-[200px] text-white font-semibold xl:px-8 xl:py-4 px-2 py-2 rounded-md w-screen"
                                type="button">Ship Now</button>
                        </div>
                    </div>
                </template>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: "ResultComponent",
    data() {
        return {
            errors: {},
            form1: this.form,
            set_rate_loading: false,
        };
    },
    props: {
        rates: Array,
        form: Object,
        loading: Boolean,
    },
    methods: {
        setRate(rate) {
            localStorage.setItem('selected_rate', JSON.stringify(rate));
            this.form1.rate = rate;
            this.set_rate_loading = true;
            this.errors = {};
            this.$axios
                .post("package/set-rate", this.form1, {
                    headers: this.$headers
                })
                .then((response) => {
                    console.log(response)
                    this.$router.push('address');
                })
                .catch((error) => {
                    this.errors = error.response;
                })
                .finally(() => {
                    this.set_rate_loading = false;
                });
        },
    },
};
</script>