<template>
    <LoaderComponent v-if="loading"></LoaderComponent>

    <section class="container mx-auto py-5 sm:px-10 px-3 space-y-5">
        <StepComponent></StepComponent>
        <DangerAlert :message="errors.message"></DangerAlert>

        <div class="grid 2xl:grid-cols-8 gap-5 ">

            <div class="2xl:col-span-6 py-10 px-5 bg-[#F5F3F6] rounded">
                <h3 class="mb-4 text-2xl font-semibold leading-none text-gray-900"> Custom declaration</h3>

                <template v-if="form.shipping_total > 2500">
                    <div
                        class="p-4 mb-5 text-sm text-black border border-yellow-300 rounded-lg bg-yellow-50 dark:bg-gray-800 dark:text-yellow-400 dark:border-yellow-800">
                        U.S Customs requires additional documents for international
                        shipments with values of $2,500.00 or more. Please visit the <a class="text-red-600 underline"
                            href="https://www.trade.gov/filing-your-export-shipments-through-automated-export-system-aes"
                            target="_blank">U.S Trade Administration</a> website for additional information. Once the
                        documents
                        are submitted, please provide the ITN number from the
                        Electronic Export Information (EEI) submission below to avoid delays or returns by customs or the
                        carrier.
                    </div>
                </template>

                <form action="#" class="space-y-5">

                    <div class="grid gap-4 mb-4 sm:grid-cols-2 grid-cols-1">
                        <div class="sm:w-auto w-full sm:col-span-1 col-span-2">
                            <label for="export" class="block mb-2 text-sm font-medium text-gray-900"> Export Reason</label>
                            <select type="text" v-model="form.package_type"
                                :class="{ 'border-red-500': errors.package_type }"
                                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5 blue-500"
                                required>
                                <option value="">Select</option>
                                <option value="commercial">Commercial</option>
                                <option value="gift">Gift</option>
                                <option value="sample">Sample</option>
                                <option value="personal">Personal</option>
                            </select>
                            <span class="text-red-600">{{ errors?.errors?.package_type }}</span>
                        </div>
                        <div class="sm:w-auto w-full sm:col-span-1 col-span-2">
                            <label for="manufacture" class="block mb-2 text-sm font-medium text-gray-900">Manufacture
                                Country</label>
                            <select type="text" v-model="form.country"
                                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5 blue-500"
                                required>
                                <option value="">Select</option>
                                <template v-for="country in countries" :key="country.id">
                                    <option :value="country.id">{{ country.name }}</option>
                                </template>
                            </select>
                            <span class="text-red-600">{{ errors?.errors?.country }}</span>
                        </div>

                        <div class="w-full sm:col-span-1 col-span-2" v-if="form.shipping_total > 2500">
                            <label for="code" class="block mb-2 text-sm font-medium text-gray-900">ITN</label>
                            <input type="text" v-model="form.itn"
                                class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5"
                                placeholder="" required="">
                            <span class="text-red-600">{{ errors?.errors?.itn }}</span>
                        </div>

                        <div class="w-full col-span-2 space-y-5 py-2">
                            <!-- <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Veniam dolor nam ducimus. Placeat
                                error fugit illo impedit eius corrupti commodi.</p> -->
                            <template v-for="box in pkg.boxes" :key="box.id">
                                <p class="uppercase">
                                    <b>Dimension:</b> {{ box.length }} x {{ box.width }} x {{ box.height }} {{ box.dim_unit
                                    }} <br>
                                    <b>Weight:</b> {{ box.weight }} {{ box.weight_unit }}
                                </p>
                            </template>
                        </div>

                        <template v-for="(item, index) in form.items" :key="item.id">
                            <div class="col-span-2 grid sm:grid-cols-2 grid-cols-1 gap-4">
                                <div class="w-full sm:col-span-1 col-span-2">
                                    <label for="description"
                                        class="block mb-2 text-sm font-medium text-gray-900">Description</label>
                                    <input type="text" v-model="item.description"
                                        class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5"
                                        :class="{ 'border-red-500': errors[`items.${index}.description`] }"
                                        placeholder="Men's blue cotton shirt" required>
                                    <span class="text-red-600">{{ errors[`items.${index}.description`] }}</span>
                                </div>
                                <div class="grid sm:grid-cols-5 items-start gap-4">
                                    <div class="col-span-1">
                                        <label for="code" class="block mb-2 text-sm font-medium text-gray-900">Code</label>
                                        <input type="text" v-model="item.hs_code"
                                            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5"
                                            :class="{ 'border-red-500': errors[`items.${index}.hs_code`] }" placeholder=""
                                            required="">
                                        <span class="text-red-600">{{ errors[`items.${index}.hs_code`] }}</span>
                                    </div>
                                    <div class="col-span-1 relative">
                                        <label for="quantity"
                                            class="block mb-2 text-sm font-medium text-gray-900">Quantity</label>
                                        <input type="text" v-model="item.quantity" @keyup="calculateTotal()"
                                            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5"
                                            :class="{ 'border-red-500': errors[`items.${index}.quantity`] }" placeholder=""
                                            required="">
                                        <span class="text-red-600">{{ errors[`items.${index}.quantity`] }}</span>
                                    </div>
                                    <div class="col-span-1 relative">

                                        <label for="price" class="block mb-2 text-sm font-medium text-gray-900">Unit
                                            Price</label>
                                        <input type="text" v-model="item.unit_price" @keyup="calculateTotal()"
                                            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5"
                                            :class="{ 'border-red-500': errors[`items.${index}.unit_price`] }"
                                            placeholder="" required="">
                                        <span class="text-red-600">{{ errors[`items.${index}.unit_price`] }}</span>
                                    </div>
                                    <div class="col-span-1 relative">
                                        <label for="export"
                                            class="block mb-2 text-sm font-medium text-gray-900">Batteries</label>
                                        <select type="text" v-model="item.batteries"
                                            :class="{ 'border-red-500': errors[`items.${index}.batteries`] }"
                                            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-600 focus:border-blue-600 block w-full p-2.5 blue-500"
                                            required>
                                            <option value="">Select</option>
                                            <option value="0">No Battery</option>
                                            <option value="1">Simple Batteries</option>
                                            <option value="2">Batteries Packaed with Equipment</option>
                                            <option value="3">Batteries Contained in Equipment</option>
                                        </select>
                                        <span class="text-red-600">{{ errors[`items.${index}.batteries`] }}</span>
                                    </div>
                                    <div class="self-end pb-2.5 pr-5 text-right" v-if="index != 0">
                                        <button @click="removeItem(index)"><i
                                                class="fa-solid fa-trash-can fa-xl"></i></button>
                                    </div>
                                </div>
                                <!-- <div class="col-span-2 space-y-10">
                                    <p>Subject to restricted items List</p>
                                    <hr class="bg-gray-300 h-[1.2px] w-full">
                                </div> -->
                            </div>
                        </template>

                        <div class="col-span-2 space-y-5">
                            <div class="sm:space-y-0 space-y-5 sm:flex justify-between items-start">
                                <button class="text-[#EDAC24]" @click="addItem"> + Add another item</button>
                                <!-- <h4 class="font-medium text-xl text-right">Sub total: <span class="text-base">$
                                        100:00</span></h4> -->
                            </div>
                            <hr class="bg-gray-300 h-[2px] w-full">
                            <h4 class="font-medium text-xl text-right">Custom Total: <span class="text-base">$ {{
                                form.shipping_total
                            }}</span></h4>
                        </div>
                    </div>

                    <button type="button" @click="handleSubmit"
                        class="text-white bg-[#ffcb5a] hover:bg-[#f7a900] focus:ring-4 focus:outline-none font-medium rounded-lg text-sm px-5 py-2.5 text-center">
                        {{ loading ? "Loading ..." : "Save & Update" }}
                    </button>
                </form>
            </div>

            <div class="2xl:col-span-2 2xl:order-none order-first  bg-[#F5F3F6]">
                <div class="w-full py-3 text-center bg-[#f7a900] text-white font-bold"><i
                        class="fa-solid fa-circle-exclamation"></i> <span>Shipment Information</span></div>
                <div class="p-4 space-y-5">
                    <div
                        class="w-full max-w-sm p-4 bg-white border border-gray-200 rounded-lg shadow sm:p-6 dark:bg-gray-800 dark:border-gray-700">

                        <p class="text-sm font-normal text-gray-500 dark:text-gray-400">Connect with one of our available
                            service providers and choose best rates.</p>
                        <ul class="my-4 space-y-3">
                            <template v-for="rate in rates" :key="rate.id">
                                <li>
                                    <div @click="updateRate(rate)"
                                        :class="{ 'bg-yellow-400': pkg.service_code == rate.type, 'bg-gray-100': pkg.service_code != rate.type }"
                                        class=" cursor-pointer flex items-center p-3 text-sm font-bold text-gray-900 rounded-lg hover:bg-yellow-500 group hover:shadow dark:bg-gray-600 dark:hover:bg-gray-500 dark:text-white">
                                        <p class="flex-1 ms-3">{{ rate.name }}</p>
                                        <p class="inline-flex items-center justify-center px-2 py-0.5 ms-3 text-xs font-medium text-gray-500 bg-gray-200 rounded dark:bg-gray-700 dark:text-gray-400">${{
                                                rate.total }}</p>
                                    </div>
                                </li>
                            </template>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div class="flex justify-between items-center px-10 py-5">
            <BackButton></BackButton>
            <button type="button" @click="bookingConfirmation"
                class="text-white bg-[#ffcb5a] hover:bg-[#f7a900] focus:ring-4 focus:outline-none font-medium rounded-lg text-sm px-5 py-2.5 text-center">
                Next
            </button>
        </div>
    </section>
</template>

<script>
import BackButton from './Global/BackButton.vue';
import StepComponent from './StepComponent.vue';
import LoaderComponent from './Partials/LoaderComponent.vue';
import DangerAlert from './Global/Alerts/DangerAlert.vue';

export default {
    name: "CustomDeclaration",
    data() {
        return {
            loading: false,
            pkg: {},
            form: {
                ship_to: "",
                ship_from: "",
                country: "",
                package_type: "",
                itn: "",
                items: [],
                shipping_total: 0,
            },
            update_rate_form: {
                package_id: "",
                rate: {}
            },
            countries: [],
            rates: [],
            errors: {}
        };
    },
    methods: {
        addItem() {
            this.form.items.push({
                description: "",
                hs_code: "",
                quantity: 1,
                unit_price: 0,
                batteries: "",
            });
        },
        removeItem(index) {
            this.form.items.splice(index, 1);
        },
        fetchRecords() {
            const url = "data"
            this.$axios
                .post(url, {
                    headers: this.$headers,
                })
                .then((response) => {
                    this.countries = response.data.data.countries;
                })
                .catch((error) => {
                    console.log(error);
                    window.scrollTo(0, 0);
                });
        },
        handleSubmit() {

            this.fetchRates();

            this.loading = true;
            this.errors = {};

            this.$axios
                .post("package/set-custom", this.form, {
                    headers: this.$headers
                })
                .then((response) => {
                    if (response.data.success == true) {
                        // this.$router.push('booking');
                    }
                    this.loading = false;
                })
                .catch((error) => {
                    this.errors = error.response.data;
                    this.loading = false;
                    window.scrollTo(0, 0);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        fetchPackage() {
            this.loading = true;
            const url = "package/get-package"
            this.$axios
                .post(url, null, {
                    headers: this.$headers,
                })
                .then((response) => {
                    this.pkg = response.data.data.package;
                    this.form.items = this.pkg.package_items;
                    this.form.package_type = this.pkg.package_type;
                    this.form.country = this.pkg?.package_items[0]?.origin_country;
                    this.calculateTotal();
                    this.loading = false;
                    this.fetchRates();
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    this.loading = false;
                    window.scrollTo(0, 0);
                });
        },
        calculateTotal() {
            let final_amount = 0;
            this.form.items.forEach((item) => {
                final_amount += parseFloat(item.unit_price) * parseInt(item.quantity);
            });
            this.form.shipping_total = parseFloat(final_amount).toFixed(2);
        },
        fetchRates() {

            let pieces = [];
            this.pkg.boxes.forEach(function (value) {
                let piece = {
                    length: value.length.toString(),
                    width: value.width.toString(),
                    weight: value.weight.toString(),
                    height: value.height.toString(),
                };
                pieces.push(piece);
            });

            var address_type = this.pkg?.ship_to?.is_residential == 1 ? true : false;

            let quote_params = {
                ship_from_country_code: this.pkg.ship_from.country_code,
                ship_from_postal_code: this.pkg.ship_from.zip_code,
                ship_to_country_code: this.pkg.ship_to.country_code,
                ship_to_postal_code: this.pkg.ship_to.zip_code,
                ship_to_city: this.pkg.ship_to.city,
                customs_value: this.pkg.shipping_total,
                address_type: address_type,
                dimensions: pieces,
            };

            this.loading = true;

            this.errors = {};
            const url = "rates"
            this.$axios
                .post(url, quote_params, {
                    headers: this.$headers,
                })
                .then((response) => {
                    this.rates = response.data.data;
                    this.loading = false;
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    this.dimension_modal = true;
                    this.loading = false;
                    window.scrollTo(0, 0);
                });
        },
        updateRate(rate) {
            this.loading = true;
            this.update_rate_form.package_id = this.pkg.id;
            this.update_rate_form.rate = rate;
            this.errors = {};

            this.$axios
                .post("package/update-rate", this.update_rate_form, {
                    headers: this.$headers
                })
                .then((response) => {
                    console.log(response);
                    this.fetchPackage();
                    this.handleSubmit();
                })
                .catch((error) => {
                    this.errors = error.response;
                    window.scrollTo(0, 0);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        bookingConfirmation() {
            this.rates.forEach(rate => {
                if (rate.type == this.pkg.service_code) {
                    this.updateRate(rate);
                }
            });

            this.$router.push("booking");
        }
    },
    mounted() {
        this.fetchPackage();
        this.addItem();
        this.fetchRecords();
    },
    components: { 
        BackButton, 
        StepComponent, 
        DangerAlert, 
        LoaderComponent 
    }
};
</script>